.main {
  padding: 20px;
  height: 100vh;
  display: flex;
  width: 100vw;
  margin: 0 auto;
}
.sidebar {
  width: 100%;
  width: 400px;
  border-radius: 5px;
  background-color: rgb(51, 1, 1);
  box-sizing: 0 0 2px gray;
  margin-right: 20px;
  color: white;
}
.sidebar h6 {
  font-size: 30px;
  margin-bottom: 20px;
}
.content {
  width: 100%;
}
.header {
  height: 10vh;
  width: 100%;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color: white;
}
.body {
  height: calc(100vh - 20vh);
  width: 100%;
  margin-bottom: 20px;
  box-shadow: 0 0 2px gray;
  background-color: white;
  flex-grow: 1;
}
.logo {
  font-size: 1.5rem;
  margin: 20px 0;
  text-align: center;
}
.menu {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.menu-item {
  padding: 6px 6px 6px 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  text-decoration: none;
  color: white;
  cursor: pointer;
  width: 200px;
}
.menu-item i {
  font-size: 20px;
}
.menu-item a {
  font-size: 18px;
  text-decoration: none;
  color: white;
  cursor: pointer;
}

.active {
  background-color: white;
  color: rgb(0, 0, 0);
}
.active a {
  color: rgb(0, 0, 0);
}
.header {
  display: flex;
  align-items: center;
  justify-content: end;
}
.header .header-content {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: end;
  font-size: 20px;
  color: black;
  gap: 10px;
}
.header .header-content span {
  font-size: 22px;
}
.header .header-content span:hover {
  color: red;
}
